<template>
  <div class="add-teacher">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-tabs class="mb10" v-model="tabPick" @tab-click="handleClick">
        <el-tab-pane label="基本情况" name="basic"></el-tab-pane>
        <el-tab-pane label="工作经历" name="resume"></el-tab-pane>
        <el-tab-pane label="获奖情况" name="award"></el-tab-pane>
      </el-tabs>
      <transition-group mode="out-in" name="el-fade-in-linear">
        <add-edit-form key="basic" v-show="tabPick === 'basic'"></add-edit-form>
        <div key="resume" v-show="tabPick === 'resume'">
          <resize-table>
          <table class="table resume-table">
            <thead>
            <tr>
              <th width="35%">起止时间</th>
              <th width="20%">教授科目</th>
              <th width="15%">年级</th>
              <th width="15%">班级</th>
              <th width="15%">是否班主任</th>
            </tr></thead>
            <tbody>
            <tr v-for="item in resume.list">
              <td>{{item.startTermName |substrTime}} - {{item.endTermName |substrTime}}</td>
              <td>{{item.courseName}}</td>
              <td>{{item.gradeName}}</td>
              <td>{{item.aliasName}}</td>
              <td>{{$enums.YesNoStatus.getName(item.isAdviser)}}</td>
            </tr></tbody>
          </table>
          </resize-table>
          <no-data v-show="!resume.noData"></no-data>
          <paging :data.sync="resume"></paging>
        </div>
        <div key="award" v-show="tabPick === 'award'">
          <resize-table>
          <table class="table resume-table">
            <thead>
            <tr>
              <th width="20%">项目名称</th>
              <th width="13%">类型</th>
              <th width="20%">颁奖单位</th>
              <th width="20%">获奖等级或取得的成绩</th>
              <th width="15%">获得时间</th>
              <th width="12%">证明材料</th>
            </tr></thead>
            <tbody>
            <tr v-for="item in award.list">
              <td>{{item.awardName}}</td>
              <td>TODO</td>
              <td>{{item.awardUnit}}</td>
              <td>{{item.achievement}}</td>
              <td>{{item.obtainTime | substrTime }}</td>
              <td>TODO</td>
            </tr></tbody>
          </table>
          </resize-table>
          <no-data v-show="!award.noData"></no-data>
          <paging :data.sync="award"></paging>
        </div>
      </transition-group>
    </main-part>
  </div>

</template>
<script>
  import addEditForm from './Form'

  export default {
    components: {
      addEditForm
    },
    data() {
      return {
        nav: [{name: "教师信息管理", path: "go(-1)"}, {name: "教师详情", path: ""}],
        tabPick: 'basic',
        // 工作经历
        resume:{
          url: '/teacher/queryTeacherResumePage.do',
          el: '.resume-table',
          total: 0,
          noData: true,
          list: [],
          query:{
            id: this.$route.query.id
          }
        },
        award:{
          url: '/teacher/queryTeacherAwardPage.do',
          el: '.resume-table',
          total: 0,
          noData: true,
          list: [],
          query:{
            id: this.$route.query.id
          }
        },
      }
    },
    methods: {
      // tab 切换的回调
      handleClick() {
        if(this.tabPick === 'resume'){
          this.$search(this.resume)
        }else if(this.tabPick === 'award'){
          this.$search(this.award)
        }
      }
    },
  }
</script>
